'use client'

import React, { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Sheet, SheetContent, SheetTrigger } from '@/components/ui/sheet'
import { NavigationMenu, NavigationMenuContent, NavigationMenuItem, NavigationMenuLink, NavigationMenuList, NavigationMenuTrigger } from '@/components/ui/navigation-menu'
import { useTheme } from 'next-themes'

const Header = () => {
  const [isScrolled, setIsScrolled] = useState(false)
  const [language, setLanguage] = useState('ID')
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)
  const { theme, setTheme } = useTheme()

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50)
    }
    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  const navigationItems = [
    {
      title: 'Beranda',
      href: '/',
    },
    {
      title: 'Profil',
      items: [
        { title: 'Visi & Misi', href: '/profil/visi-misi' },
        { title: 'Struktur Organisasi', href: '/profil/struktur' },
        { title: 'Tugas & Fungsi', href: '/profil/tugas-fungsi' },
      ]
    },
    {
      title: 'Layanan',
      items: [
        { title: 'Perizinan Angkutan', href: '/layanan/perizinan-angkutan' },
        { title: 'Uji KIR', href: '/layanan/uji-kir' },
        { title: 'Surat Keterangan', href: '/layanan/surat-keterangan' },
      ]
    },
    {
      title: 'Informasi',
      items: [
        { title: 'Berita', href: '/informasi/berita' },
        { title: 'Pengumuman', href: '/informasi/pengumuman' },
        { title: 'Kondisi Jalan', href: '/informasi/kondisi-jalan' },
      ]
    },
    {
      title: 'Kontak',
      href: '/kontak',
    },
  ]

  const toggleLanguage = () => {
    setLanguage(prev => prev === 'ID' ? 'EN' : 'ID')
  }

  const toggleTheme = () => {
    setTheme(theme === 'dark' ? 'light' : 'dark')
  }

  return (
    <header className={`sticky top-0 z-50 w-full transition-all duration-300 ${
      isScrolled 
        ? 'bg-white/95 dark:bg-slate-900/95 backdrop-blur-md shadow-lg border-b border-blue-100 dark:border-blue-800' 
        : 'bg-gradient-to-r from-blue-600 to-blue-800 text-white'
    }`}>
      {/* Top Bar */}
      <div className="border-b border-blue-500/20">
        <div className="container mx-auto px-4 py-2">
          <div className="flex justify-between items-center text-sm">
            <div className="flex items-center space-x-4">
              <span className="text-blue-100">📧 dinasperhubungankabupatenbarru@gmail.com</span>
              <span className="text-blue-100">📞 (0427) xxxxx</span>
            </div>
            <div className="flex items-center space-x-2">
              <Button
                variant="ghost"
                size="sm"
                onClick={toggleLanguage}
                className="text-blue-100 hover:text-white hover:bg-blue-700/50"
              >
                {language}
              </Button>
              <Button
                variant="ghost"
                size="sm"
                onClick={toggleTheme}
                className="text-blue-100 hover:text-white hover:bg-blue-700/50"
              >
                {theme === 'dark' ? '☀️' : '🌙'}
              </Button>
            </div>
          </div>
        </div>
      </div>

      {/* Main Navigation */}
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between h-16">
          {/* Logo and Title */}
          <div className="flex items-center space-x-4">
            <div className="relative">
              {/* Dinas Perhubungan Logo */}
              <div className="w-12 h-12 bg-gradient-to-br from-blue-600 to-blue-800 rounded-lg flex items-center justify-center shadow-lg">
                <div className="text-white font-bold text-lg">🚧</div>
              </div>
            </div>
            <div>
              <h1 className={`font-poppins font-bold text-lg ${
                isScrolled ? 'text-blue-800 dark:text-blue-200' : 'text-white'
              }`}>
                DINAS PERHUBUNGAN
              </h1>
              <p className={`text-sm ${
                isScrolled ? 'text-blue-600 dark:text-blue-300' : 'text-blue-100'
              }`}>
                Kabupaten Barru
              </p>
            </div>
          </div>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex">
            <NavigationMenu>
              <NavigationMenuList>
                {navigationItems.map((item) => (
                  <NavigationMenuItem key={item.title}>
                    {item.items ? (
                      <>
                        <NavigationMenuTrigger className={`${
                          isScrolled 
                            ? 'text-slate-700 dark:text-slate-200 hover:text-blue-600 dark:hover:text-blue-400' 
                            : 'text-white hover:text-blue-200'
                        } font-medium`}>
                          {item.title}
                        </NavigationMenuTrigger>
                        <NavigationMenuContent>
                          <div className="grid w-48 gap-1 p-2">
                            {item.items.map((subItem) => (
                              <NavigationMenuLink
                                key={subItem.title}
                                href={subItem.href}
                                className="block px-3 py-2 text-sm rounded-md hover:bg-blue-50 dark:hover:bg-blue-900 text-slate-700 dark:text-slate-200 hover:text-blue-600 dark:hover:text-blue-400"
                              >
                                {subItem.title}
                              </NavigationMenuLink>
                            ))}
                          </div>
                        </NavigationMenuContent>
                      </>
                    ) : (
                      <NavigationMenuLink
                        href={item.href}
                        className={`px-4 py-2 font-medium transition-colors ${
                          isScrolled 
                            ? 'text-slate-700 dark:text-slate-200 hover:text-blue-600 dark:hover:text-blue-400' 
                            : 'text-white hover:text-blue-200'
                        }`}
                      >
                        {item.title}
                      </NavigationMenuLink>
                    )}
                  </NavigationMenuItem>
                ))}
              </NavigationMenuList>
            </NavigationMenu>
          </nav>

          {/* Mobile Menu Button */}
          <div className="md:hidden">
            <Sheet open={isMobileMenuOpen} onOpenChange={setIsMobileMenuOpen}>
              <SheetTrigger asChild>
                <Button
                  variant="ghost"
                  size="sm"
                  className={`${
                    isScrolled 
                      ? 'text-slate-700 dark:text-slate-200' 
                      : 'text-white'
                  }`}
                >
                  <div className="w-6 h-6 flex flex-col justify-center space-y-1">
                    <div className="w-full h-0.5 bg-current"></div>
                    <div className="w-full h-0.5 bg-current"></div>
                    <div className="w-full h-0.5 bg-current"></div>
                  </div>
                </Button>
              </SheetTrigger>
              <SheetContent side="right" className="w-80">
                <div className="flex flex-col space-y-4 mt-8">
                  {navigationItems.map((item) => (
                    <div key={item.title}>
                      {item.items ? (
                        <div>
                          <h3 className="font-semibold text-slate-900 dark:text-slate-100 mb-2">
                            {item.title}
                          </h3>
                          <div className="pl-4 space-y-2">
                            {item.items.map((subItem) => (
                              <a
                                key={subItem.title}
                                href={subItem.href}
                                className="block py-2 text-slate-600 dark:text-slate-400 hover:text-blue-600 dark:hover:text-blue-400"
                                onClick={() => setIsMobileMenuOpen(false)}
                              >
                                {subItem.title}
                              </a>
                            ))}
                          </div>
                        </div>
                      ) : (
                        <a
                          href={item.href}
                          className="block py-2 font-medium text-slate-900 dark:text-slate-100 hover:text-blue-600 dark:hover:text-blue-400"
                          onClick={() => setIsMobileMenuOpen(false)}
                        >
                          {item.title}
                        </a>
                      )}
                    </div>
                  ))}
                </div>
              </SheetContent>
            </Sheet>
          </div>
        </div>
      </div>
    </header>
  )
}

export default Header
